body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  width: 100%;
}


.warCouncil{
  justify-content: center;
}

.Menu{
    display: flex;
    width: 100%;
   justify-content: space-between;
   background-color: #7C0A02;
   height: 90px; 
   padding-top: 1%;
}

.website-title{
    color: #E3DCC2;
}

h2{
    padding-right: 10%;
    padding-left:3%;
    background-color: #7C0A02;
    height: 50px;

}

.menuItems{
    
    display: flex;
    justify-content: space-evenly;
    width: 500px;
    background-color: #7C0A02;
    margin-left: 20px;
}

.menuItems a{
    color: #E3DCC2;
}

.Avatar{
    width: 30px;
    background-color: transparent;
}

.loggedInMenu{
    display: flex;
}

.container{
    position: relative;
    width: 100%;
    padding-bottom: 50px;

}


.bannerText{
    position: absolute;
    top: 50%;
    left: 30%;
    width: 60%;
    transform: translate(
        -50%, -50%);
    color: black;
    font-size: 600%;
    z-index: 3;
}
.bannerImage{
    width:100%;
    object-position: center;
    
    -webkit-filter: opacity(80%);
    
            filter: opacity(80%);
   
}

.description{
    height: 20%;
    padding-top: 5%;
    padding-bottom: 5%;
    width: 100%;
    color: #E3DCC2;
    font-size: 250%;
}

.sectionBreak{
    width: 100px;
}

.storyPromo{
    width: 100%;
    height: 500px;
    padding-left: 50px;
    padding-right: 50px;
    display: flex;
}

.storyPromoText{
    padding-top: 3%;
    width: 60%;
    color: #7C0A02;
    font-size: 100%;
    margin-right: 2%;
    display: flex;
    flex-direction: column;
}
.mapImage{
    width: 500px;
    height: 400px;
    
}

#btn{
    background-color: #51513D;
}

.roundTablePromo{
    width: 100%;
    height: 20%;
    padding-left: 50px;
    padding-right: 50px;
    display: flex;
    background-color: #E3DCC2;
    margin-bottom: 5%;
}

.roundTableImage{
    width: 500px;
    height: 500px;
    padding-top: 3%;
}

.roundTablePromoText{
    padding-top: 6%;
    width: 60%;
    font-size: 100%;
    margin-right: 2%;
    display: flex;
    flex-direction: column;
    color: #7C0A02;
}

.menuLink{
    color: white;

}

@media  (min-width: 1350px) {
    .roundTablePromoText{
        font-size: 150%;
    }
    .storyPromoText{
        font-size: 150%;
    }
 
}

@media (max-width:1350px){
    .description{
        font-size: 150%;
    }

    .menuItems{
        display: flex;
    }

}

@media (max-width: 700px){


    .container {
        display: inline-block;
        cursor: pointer;
        
      }
      
      .bar1, .bar2, .bar3 {
        width: 35px;
        height: 5px;
        background-color: #333;
        margin: 6px 0;
        transition: 0.4s;
    
      }

    .Menu{
    width: 100%;

    }

   h2{

   }

    .menuItems{
    width: 40%;
    list-style-type: none;
    position: absolute;
    flex-direction: column;
    justify-content: flex-end;
    margin-left: -20%;
    margin-top: -15%;
    position: absolute;
    z-index: 1;
    }
    .website-title{
       
        font-size: 100%;
        width: 40%;
    }
    .description{
        font-size: 100%;
        padding-right: 5%;
      
        width: 100%;
        
    }

    .banner {
        width: 100%;
    }
    .bannerImage{
        margin-left: 0%;
        width: 100%;
    }
    .bannerText{
        font-size: 180%;
        margin-left: -25%;
        margin-top: -25%;
        width: 100%;
    }

    
    .roundTableImage{
        width: 80%;
        height: 90%;
        object-position: c;
    }
    .roundTablePromoText{
        font-size: 60%;
    width: 100%;
    }
   
    .mapImage{
        width: 80%;
        height: 80%;
        margin-top: 5%;
      
    }
    .storyPromo{
        height: 100%;
        width: 100%;
        margin: auto;
       
        margin-bottom: 5%;
        object-position: center;
        display: block;
    }
    .storyPromoText{
        font-size: 60%;
        width: 100%;
    }
    .roundTablePromo{
        width: 100%;
        object-position: center;
        display: flex;
        flex-direction: column-reverse;
    }
    .bannerText{
        transform: translate(
        -0%, -50%);
    }

    #btn{
        font-size: 60%;
    }
}
